import Vue from 'vue'
import Router from 'vue-router'

// import Home from '@/components/Home'
const Home = () => import('../components/Home')

import Picture from '@/components/Picture'
import Demopage from '@/components/Demopage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/Home'
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home,
    },{
      path: '/Picture',
      name: 'Picture',
      component: Picture
    },

    {
      path: '/Demopage',
      name: 'Demopage',
      component: Demopage
    }


  ]
})



