<template lang="html">
  <div class="picture">
    <span class="goBack" @click="goBack">GoBack</span>
    <div class="pic_con">
      <template v-for="(item,index) in picList">
        <div :class="['row' , index % 2 == 0 ? 'rowleft' : 'rowright']" :key="index">
          <img :src="item.imgUrl" alt="">
          <div class="row_intro">
            <p class="title">{{item.title}} <span>{{item.location}}</span></p>
            <p class="intro">{{item.intro}}</p>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      picList: [
        {
          'imgUrl': require('../assets/woniu.jpg'),
          'title': '蜗牛之吻',
          'location': '——印度尼西亚三发县',
          'intro': '图中的蜗牛并不像我们看到的那样在真的接吻，这只是它们交流的一种方式。科学家们仍在研究蜗牛交配习惯的奥秘，并给出了一些惊人复杂的生物学理论。让你意想不到的是，蜗牛是世界上牙齿最多的动物，牙齿的数量达到数万颗!'
        },{
          'imgUrl': require('../assets/puluowangsi.jpg'),
          'title': '普罗旺斯的梦境',
          'location': '——法国',
          'intro': '戈尔代小镇是普罗旺斯地区最美丽的小镇之一，依山头而建，是众多被日光洗的发白的石头建筑与深深浅浅的绿色植物相拼而成的村庄。每年那里都会入选法国最美丽的小镇之一。从外到内小镇戈尔代给人的印象就是石头建筑，围墙，房子，教堂几乎都是石头堆砌而成，壮观而真实！'
        },{
          'imgUrl': require('../assets/park.jpg'),
          'title': '瓜伊哈纳斯国家公园保留地',
          'location': '——加拿大',
          'intro': '潮湿的海洋空气冷却了海达瓜伊群岛郁郁葱葱、布满苔藓的地形，使这个遥远的加拿大岛屿看起来像是一次时间的旅行，当时原始的自然主宰了西方的景观。这个岛的南部被保存为瓜伊哈纳斯国家公园保留地和海达文物古迹，这是由海达和加拿大政府共同管理的一大片陆地和海洋区域。'
        },{
          'imgUrl': require('../assets/wharf.jpg'),
          'title': '曼哈顿海滩码头',
          'location': '——加利福尼亚州',
          'intro': '最初的海滩码头建造于1901年，每年的这个时候，在码头边漫步，看着太平洋的日落，慢慢走回岸边，让你浑身充满着假日的光芒，是季节的光环映照在码头。在码头末端的“树”下是圆屋海洋研究室和水族馆，在那里游客可以看到下面冲浪的人们。也许圣诞老人今年会带来好收成。'
        },{
          'imgUrl': require('../assets/puluowangsi.jpg'),
          'title': '耶罗岛上的洞穴',
          'location': '——西班牙加那利群岛',
          'intro': '耶罗岛可能是加那利群岛最小、最南端的小岛，但它在地质学上是一个传奇。山体滑坡极大地影响了岛上的地形，比如埃尔戈尔福，就是由大约15000年前发生的大规模山体滑坡造成的。尽管地质恶劣，但岛上仍然有大约1万人口，是加那利群岛蓬勃发展的旅游业的一部分。'
        },{
          'imgUrl': require('../assets/bridge.jpg'),
          'title': '布鲁克林大桥',
          'location': '——纽约',
          'intro': '布鲁克林大桥在1883年正式通车。但是七年前，桥底的锚固结构被用来储存葡萄酒。为了帮助还清桥梁建设时的1500万美元负债，首席桥梁工程师华盛顿·罗伯林在桥梁两边的拱形斜坡下建造了这些酒窖。如今，这些酒窖也成为布鲁克林大桥的亮点'
        },{
          'imgUrl': require('../assets/ray.jpg'),
          'title': '萨罗尼克湾上方的云隙光',
          'location': '——希腊',
          'intro': '这束在云层中闪过的阳光被拍摄于希腊海岸的萨罗尼克湾。你不需要专程去爱琴海看这束光，因为它发生在世界各地。它被称为云隙光，在西方国家被称为耶稣光或上帝之梯，一般在黎明或黄昏时候出现。一抹神奇的光线透过云层洒落大地，这情景真的美极了！'
        },{
          'imgUrl': require('../assets/ice_tunnel.jpg'),
          'title': '马他奴思卡冰川里的冰隧道',
          'location': '——阿拉斯加州',
          'intro': '如果你喜欢公路旅行，开车到阿拉斯加的马他奴思卡冰川是个不错的选择。当你看到这个27英里长，4英里宽的冰隧道，那么你的徒步旅行就要开始了！跟着导游乘船沿着隧道划行，晶莹泛蓝的冰川透着丝丝寒气，把一路上的炎热感直接驱走。徜徉在这个美如仙境的冰川世界，满足惬意感油然而生！'
        },{
          'imgUrl': require('../assets/volcano.jpg'),
          'title': '爆发中的卡尔布科火山',
          'location': '——智利',
          'intro': '卡尔布科火山在沉睡了40年以后，在2015年喷发了数次。如同科幻片中世界末日一般的火山喷发，伴随着冲上天际的炽热熔岩，漫天的烟雾把火山都覆盖住了。而在浓浓黑雾之间，更是有数道闪电划破夜空，场面十分壮观，也让人对大自然的力量感到恐惧。'
        }
      ]
    }
  },
  mounted () {
    window.scrollTo(0, 0)
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
.picture {
  position: relative;
}
.goBack {
  position: fixed;
  left: 50px;
  top: 50px;
  width:100px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  border-radius: 30px;
  background-color: deepskyblue;
  cursor: pointer;
  transition: 0.3s;
  &:hover {
    box-shadow: 0px 0px 10px grey;
    transform: translate(-10px);
  }
}
.pic_con {
  width: 70%;
  min-width: 1300px;
  margin: 0 auto;
  padding-top: 30px;
}
.row {
  // display: flex;
  padding-bottom: 100px;
  overflow: hidden;
  img{
    // display: block;
    width: 640px;
    height: 360px;
    border-radius: 20px;
    box-shadow: 13px 13px 13px -3px grey;
  }
  .row_intro {
    width: 45%;
    box-sizing: border-box;
    height: 360px;
    padding: 50px 100px 50px 50px;
    .title {
      font-size: 24px;
      font-weight: 700;
      transition: 0.3s;
      text-align: left;
      margin-bottom: 50px;
      color: #000;
      text-indent: 0.5em;
      span{
        font-weight: normal;
      }
      cursor: pointer;
      &:hover {
        color: #ef6249;
      }
    }
    .intro {
      letter-spacing: 1px;
      line-height: 25px;
      text-indent: 2em;
      margin-bottom: 30px;
    }
  }
}
.rowleft {
  img {
    float: left;
  }
  .row_intro {
    float: right;
  }
}
.rowright {
  img {
    float: right;
    margin-right: 20px;
  }
  .row_intro {
    float: left;
  }
}
</style>



