import * as type from './mutation-types.js'

export const setDirection = ({commit}, data) =>{
  commit(type.SET_DIRECTION, data)
}
export const setModal = ({commit}, data) =>{
  commit(type.SET_MODAL, data)
}

export const setHistory = ({commit}, data) =>{
  commit(type.SET_HISTORY, data)
}



